.Flag{
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    height: 100vh;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: column;
        flex-direction: column;
    z-index: 300;
}

.flag-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    margin-top: 5px;
}

.flag-show {
    background-color: #ccc;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    border-radius: 2px;
    height: 18px;
    padding-left: 3px;
    padding-right: 3px;
}

.flag-show p {
    font-size: 11px;
    margin-top: 12px;
}

.flag-changer {
    display: -ms-flexbox;
    display: flex;
    background-color: #ccc;
    -ms-flex-direction: row;
        flex-direction: row;
    border-radius: 5px;
    padding: 10px;
}

.flag-input-option {
    -ms-flex-item-align: center;
        align-self: center;
    justify-self: center;
    margin-left: 5px;
    margin-right: 5px;
}

.flag-input-option input {
    border: none;
    outline: none;
    text-align: center;
    padding: 5px;
    border-radius: 5px;
    -webkit-transition: all ease-in-out 0.3s;
    -o-transition: all ease-in-out 0.3s;
    transition: all ease-in-out 0.3s;
    opacity: 0.5;
}

.flag-input-option input:focus {
    opacity: 1;
}


